.exampleContainer {
  border: solid black 2px;
  width: clamp(12rem, 2.5rem + 76vw, 50rem);
  max-width: 800px;
  margin: 5rem 1rem 1rem 1rem;
  background-color: rgba(15, 60, 65, 0.4);
}

.example {
  border: solid black 2px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5rem 1rem 5rem 1rem;
  margin: 1rem;
}

.exampleLabel {
  font-size: clamp(1rem, 0.5rem + 4vw, 2.5rem);
  color: Black;
}

.exampleContainer p {
  margin: 1rem;
}
